;\n}\n\n.app-bar .app-bar__brand h1 {\n  text-transform: uppercase;\n  font-size: 22px;\n  user-select: none;\n  color: #F2E3DB;\n}\n\n.app-bar .app-bar__navigation {\n  position: absolute;\n  top: 50px;\n  left: -180px;\n  width: 100px;\n  transition: all 0.3s;\n  padding: 8px;\n  overflow: hidden;\n}\n\n.app-bar .app-bar__navigation.open {\n  left: 0;\n  background-color: white;\n}\n\n.app-bar .app-bar__navigation ul li a {\n  display: inline-block;\n  text-decoration: none;\n  color: black;\n  padding: 14px 10px;\n  margin-bottom: 5px;\n  width: 100%;\n}\n\n\n\n.hero {\n  min-height: 400px;\n  width: 100%;\n  text-align: center;\n   \n}\n\n.hero::before {\n  content: \"\";\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  background: linear-gradient(to top, rgba(253, 252, 252, 0) 0%, rgba(0, 0, 0, 0.8) 100%);\n  z-index: 1;\n}\n\n\n\n.hero img {\n  background-position: center;\n  background-repeat: no-repeat;\n  background-size: cover;\n  -o-object-fit: cover;\n  object-fit: cover;\n  width: 100%;\n  height: 500px;\n}\n\n\n.hero__inner {\n  width: 80%;\n  position: absolute;\n  top: 40%;\n  left: 50%;\n  transform: translate(-50%, -50%);\n  text-align: center;\n  /* background-color: rgba(0, 0, 0, 0.7); */\n  color: #fff;\n  padding: 30px;\n  z-index: 1;\n}\n\n.hero__title {\n  font-family: 'Caveat', cursive;\n  color: white;\n  font-weight: 300;\n  font-size: 50px;\n}\n\n.hero__tagline {\n  color: white;\n  margin-top: 16px;\n  font-size: 20px;\n  font-weight: 300;\n}\n\n.hero-favorite{\n  min-height: 100px;\n  width: 100%;\n  text-align: center;\n  position: relative;\n   \n}\n\n.hero-favorite::before {\n  content: \"\";\n  position: absolute;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  background: linear-gradient(to top, rgba(253, 252, 252, 0) 0%, rgba(0, 0, 0, 0.8) 100%);\n  z-index: 1;\n}\n\n\n.hero-favorite img {\n  background-position: center;\n  background-repeat: no-repeat;\n  background-size: cover;\n  -o-o